































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import CWeb from '@/components/links/CWeb.vue';

@Component({
  components: {
    CWeb,
  },
  head: {
    title: {
      inner: '画像エンコーダ',
    },
  },
})
export default class ImageEncoder extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataImage: any = null;
  private dataImageSrc: string = '';
  private dataImageAlt: string = '';
  private dataImageTag: string = '';
  private dataAlert: string = '';

  // Watch
  @Watch('dataImage')
  private watchOnImageDropped(newValue: string, orgValue: string) {
    this.encodeImage();
  }

  // Methods
  private listenDragover($event: Event): void {
    (<any>$event).stopPropagation();
    (<any>$event).preventDefault();
    (<any>$event).dropEffect = 'copy';
  }
  private listenDrop($event: Event): void {
    (<any>$event).stopPropagation();
    (<any>$event).preventDefault();
    let files: object[] = (<any>$event).dataTransfer.files;
    if ((<Blob>files[0]).type.match(/^image\//)) {
      this.dataAlert = '';
      this.dataImage = files[0];
    } else {
      this.dataAlert = '画像ファイルをドロップしてください。';
      this.dataImage = null;
    }
  }
  private encodeImage(): void {
    if (this.dataImage) {
      let fileReader: FileReader = new FileReader();
      fileReader.readAsDataURL(this.dataImage);
      fileReader.onload = (): any => {
        // Display image
        let imageURL: string | ArrayBuffer | null;
        imageURL = fileReader.result;
        this.dataImageSrc = String(imageURL);
        this.dataImageAlt = this.dataImage['name'];
        this.dataImageTag = '<img '
          + 'src="' + this.dataImageSrc + '" '
          + 'alt="' + this.dataImageAlt + '" '
          + '/>'
      };
    }
  }
}
